:root {
  --primary-100: hsl(289, 100%, 72%);
  --primary-200: hsl(276, 100%, 81%);
  --primary-300: hsl(293, 100%, 63%);
  --primary-400: hsl(276, 55%, 52%);
  --primary-500: hsl(264, 100%, 61%);
  --primary-600: hsl(271, 36%, 24%);
  --primary-700: hsl(271, 15%, 43%);
  --user-background: hsl(276, 41%, 93%);
  --light-grey: hsl(206, 6%, 79%);
  --dark-grey: hsl(270, 7%, 64%);
  --app-background: hsl(270, 20%, 96%);
  --clr-black: #000;
  --clr-white: hsl(0, 0%, 100%);
  --gradient-start: var(--primary-300);
  --gradient-end: var(--primary-500);
  --gradient: linear-gradient(
    225deg,
    var(--gradient-start) 0%,
    var(--gradient-end) 100%
  );
  --reverse-gradient: linear-gradient(
    225deg,
    var(--gradient-end) 0%,
    var(--gradient-start) 100%
  );
  --msg-radius: 0.625rem 0.625rem 0.25rem 0.625rem;
  --msg-radius-left: 0.625rem 0.625rem 0.625rem 0.25rem;
  --ff-sans: "Rubik", Arial, Helvetica, sans-serif;
  --ff-mono: "Source Code Pro", monospace;
  --ff-serif: Cambria, Cochin, Georgia, serif;
  --fs-xlarge: 2.5rem;
  --fs-large: 1rem;
  --fs-med: 0.75rem;
  --fs-small: 0.6875rem;
  --fs-xsmall: 0.5rem;
  --gap: 1.25rem;
  --columns: 2;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-style: normal;
  font-weight: normal;
}

html,
body {
  margin: 0px auto;
  width: 100%;
  box-sizing: border-box;
  background: var(--app-background);
  color: var(--clr-black);
}

body {
  font-family: var(--ff-sans);
  font-weight: 400;
  font-size: var(--fs-med);
  line-height: 1.6;
  position: relative;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

h1 {
  margin: 0;
  margin-bottom: 1.5rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  font-size: var(--fs-xlarge);
  color: var(--primary-600);
}

p {
  font-size: var(--fs-large);
  color: var(--dark-grey);
  line-height: 1.75rem;
}

.message {
  display: inline-block;
  margin-bottom: 0.5rem;
  padding: 0.375rem 0.5rem;
  width: auto;
  max-width: 8rem;
  background-color: var(--clr-white);
  border-radius: var(--msg-radius);
  box-shadow: 0px 10px 5px -5px rgba(62, 39, 83, 0.05);
  font-size: var(--fs-xsmall);
  color: var(--primary-700);
  align-self: flex-end;
  line-height: 0.6875rem;
}
.message--user, .message--pricing {
  align-self: flex-start;
  box-shadow: none;
  border-radius: var(--msg-radius-left);
}
.message--user {
  background-color: var(--user-background);
  color: var(--primary-400);
}
.message--images {
  display: flex;
  gap: 0.5rem;
  background-color: transparent;
  max-width: 100%;
  box-shadow: none;
  padding: 0;
  padding-top: 0.5rem;
}
.message--images img {
  display: block;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 0.625rem;
  box-shadow: 0px 10px 5px -5px rgba(62, 39, 83, 0.05);
}
.message--pricing {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: space-between;
  padding: 0.6875rem 1rem 0.625rem 1.8125rem;
  background: var(--reverse-gradient);
  color: var(--clr-white);
  width: 100%;
  max-width: 10rem;
}
.message--pricing .price {
  font-size: var(--fs-med);
  font-weight: 700;
}
.message--pricing::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 1rem;
  transform: translate(-50%, -50%);
  height: 0.75rem;
  width: 0.75rem;
  border-radius: 50%;
  border: 1px solid var(--primary-100);
}

.phone {
  display: flex;
  position: relative;
  padding: 0.59375rem;
  height: 31.5625rem;
  width: 15.4375rem;
  border-radius: 1.875rem;
  box-shadow: 0px 1.875rem 3.75rem -0.625rem rgba(62, 39, 83, 0.25);
  background-color: var(--clr-white);
}
.phone::before {
  content: "";
  position: absolute;
  display: block;
  height: 1.8125rem;
  width: 8.0625rem;
  background-color: var(--clr-white);
  border-radius: 0 0 0.875rem 0.875rem;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.phone__screen {
  display: flex;
  flex-direction: column;
  height: 30.1875rem;
  width: 14.25rem;
  flex-shrink: 0;
  border-radius: 1.25rem;
  background: var(--app-background);
  overflow: hidden;
}
.phone__screen--header {
  display: flex;
  align-items: center;
  margin-bottom: 0.6875rem;
  padding-inline: 1rem;
  padding-top: 1.125rem;
  font-size: var(--fs-small);
  background: var(--gradient);
  height: 4.125rem;
  width: 100%;
  border-radius: 1.25rem 1.25rem 0.375rem 0.375rem;
  box-shadow: 0px 0px 13px 0px rgba(62, 39, 83, 0.25);
}
.phone__screen--avatar {
  display: block;
  margin-right: 0.5rem;
  height: 1.5rem;
  width: 1.5rem;
  border-radius: 1.5rem;
  border: 1px solid var(--clr-white);
}
.phone__screen--arrow {
  display: inline-block;
  margin-right: 0.5rem;
  padding: 0.125rem;
  border: solid var(--clr-white);
  border-width: 0 0.125rem 0.125rem 0;
  border-radius: 0.0625rem;
  transform: rotate(135deg);
}
.phone__screen--options {
  margin-left: auto;
  position: relative;
  height: 0.125rem;
  width: 0.125rem;
  background-color: var(--clr-white);
  border-radius: 50%;
}
.phone__screen--options::before, .phone__screen--options::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  background-color: inherit;
  border-radius: inherit;
}
.phone__screen--options::before {
  top: -0.25rem;
}
.phone__screen--options::after {
  top: 0.25rem;
}
.phone__screen--content {
  display: flex;
  flex-direction: column;
  padding-inline: 0.46875rem;
}
.phone__textarea {
  display: flex;
  align-items: center;
  position: relative;
  background-color: var(--clr-white);
  color: var(--light-grey);
  margin: auto;
  margin-bottom: 0.625rem;
  padding-left: 1.21375rem;
  height: 2.1193125rem;
  width: 13.3125rem;
  border-radius: 1.059625rem;
  font-size: 0.5625rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.07px;
}
.phone__textarea::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0.25rem;
  background-color: var(--primary-600);
  height: 1.5625rem;
  width: 1.5625rem;
  border-radius: 1.5625rem;
}
.phone__textarea::after {
  content: "";
  position: absolute;
  display: inline-block;
  margin-right: 0.5rem;
  padding: 0.1875rem;
  border: solid var(--clr-white);
  border-width: 0 0.1875rem 0.1875rem 0;
  border-radius: 0.09375rem;
  transform: rotate(-45deg);
  right: 0.3125rem;
}

.user__name {
  color: var(--clr-white);
  font-weight: 500;
}
.user__status {
  color: var(--primary-200);
  font-size: 0.5rem;
}

.attribution {
  font-size: var(--fs-small);
  text-align: center;
  margin-bottom: 2.5rem;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 4rem 1.25rem;
  width: 100%;
}
.container::before, .container::after {
  content: "";
  display: block;
  height: 63.625rem;
  width: 31.875rem;
  border-radius: 15.9375rem;
  background: var(--gradient);
}
.container::before {
  position: absolute;
  left: -20.15625rem;
  top: -32.8125rem;
}
.container::after {
  opacity: 0.03;
  position: fixed;
  bottom: -30rem;
  right: -20.15625rem;
  z-index: -1;
}
@media (min-width: 45.9375em) {
  .container {
    padding-top: 9.25rem;
    padding-inline: 0;
    max-width: 51.0625rem;
  }
  .container::before {
    top: -21.25rem;
    left: 50%;
    transform: translate(-860px);
  }
  .container::after {
    bottom: -12.5rem;
    right: 50%;
    transform: translate(860px);
  }
}

.content {
  text-align: center;
  max-width: 27.8125rem;
}
@media (min-width: 45.9375em) {
  .content {
    text-align: left;
  }
}

.flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  z-index: 2;
}
@media (min-width: 45.9375em) {
  .flex {
    gap: 0;
    justify-content: space-between;
  }
}